import cmdIcon from "../cmd-icon/cmd-icon.vue"

/**
 * 导航栏组件
 * @description 避免用过多的元素填满导航栏。一般情况下，一个『返回按钮』、一个『标题』、一个『当前视图的控件』就足够
 * @tutorial https://ext.dcloud.net.cn/plugin?id=199
 * @property {Boolean} fixed 导航栏固定到页面顶部 - 默认true
 * @property {Boolean} back 导航栏左侧返回按钮 - 默认false,点击返回上层
 * @property {String} left-text 导航栏左侧文字 - 可同显导航栏左侧返回按钮
 * @property {String} left-title 导航栏左侧标题 - 不可显示导航栏左侧文字、图标一、导航栏中心标题
 * @property {String} title 导航栏中心标题
 * @property {String} right-text 导航栏右侧文字
 * @property {String} right-color 导航栏右侧文字颜色
 * @property {String} font-color 导航栏内文字、图标的颜色
 * @property {String} background-color 导航栏背景颜色
 * @property {String} icon-one 导航栏图标一 - 不可与导航栏左侧返回按钮、导航栏左侧标题同显
 * @property {String} icon-two 导航栏图标二
 * @property {String} icon-three 导航栏图标三 - 须显有导航栏图标二
 * @property {String} icon-four 导航栏图标四 - 不可与导航栏右侧文字同显
 * @event {Function} iconOne 导航栏图标一 点击事件
 * @event {Function} iconTwo 导航栏图标二 点击事件
 * @event {Function} iconThree 导航栏图标三 点击事件
 * @event {Function} iconFour 导航栏图标四 点击事件
 * @event {Function} leftText 导航栏左侧文字 点击事件
 * @event {Function} rightText 导航栏右侧文字 点击事件
 * @example <cmd-nav-bar title="基本"></cmd-nav-bar>
 */
export default {
  name: 'cmd-nav-bar',

  components: {
    cmdIcon
  },

  props: {
    /**
     * 固定到页面顶部
     */
    fixed: {
      type: Boolean,
      default: true
    },
    /**
     * 文字图标颜色
     */
    fontColor: {
      type: String,
      default: ''
    },
    /**
     * 导航栏背景颜色
     */
    backgroundColor: {
      type: String,
      default: ''
    },
    /**
     * 导航栏标题
     */
    title: {
      type: String,
      default: ''
    },
    /**
     * 导航栏左侧返回按钮，默认点击返回上层
     */
    back: {
      type: Boolean,
      default: false
    },
    /**
     * 左侧文字可同显返回按钮
     */
    leftText: {
      type: String,
      default: ''
    },
    /**
     * 左侧显示标题，不可显示左侧文字图标
     */
    leftTitle: {
      type: String,
      default: ''
    },
    /**
     * 右侧文字
     */
    rightText: {
      type: String,
      default: ''
    },
    /**
     * 右侧文字颜色
     */
    rightColor: {
      type: String,
      default: ''
    },
    /**
     * 图标一，不可与返回按钮,左侧标题同显
     */
    iconOne: {
      type: String,
      default: ''
    },
    /**
     * 图标二
     */
    iconTwo: {
      type: String,
      default: ''
    },
    /**
     * 图标三，须显有图标二
     */
    iconThree: {
      type: String,
      default: ''
    },
    /**
     * 图标四，不可与右侧文字同显
     */
    iconFour: {
      type: String,
      default: ''
    }
  },

  computed: {
    /**
     * 设置中心标题
     */
    setCenterTitle() {
      let centerTitle = '';
      if (this.title) {
        if (this.title.length > 8) {
          centerTitle = this.title.slice(0, 6) + '...';
        } else {
          centerTitle = this.title;
        }
      }
      return centerTitle;
    },
    /**
     * 设置标题图标颜色
     */
    setFontColor() {
      let fontColor = '#000';
      if (this.fontColor) {
        fontColor = this.fontColor;
      }
      return fontColor;
    },
    /**
     * 设置背景颜色
     */
    setBackgroundColor() {
      let backgroundColor = '#fff';
      if (this.backgroundColor) {
        backgroundColor = `background: ${this.backgroundColor};`;
      }
      return backgroundColor;
    }
  },

  methods: {
    /**
     * 图标一点击事件
     */
    $_iconOneClick() {
      if (this.back) {
        uni.navigateBack()
      } else {
        this.$emit("iconOne");
      }
    },
    /**
     * 图标二点击事件
     */
    $_iconTwoClick() {
      this.$emit("iconTwo");
    },
    /**
     * 图标三点击事件
     */
    $_iconThreeClick() {
      this.$emit("iconThree");
    },
    /**
     * 图标四点击事件
     */
    $_iconFourClick() {
      this.$emit("iconFour");
    },
    /**
     * 左侧文字点击事件
     */
    $_leftTextClick() {
      this.$emit("leftText");
    },
    /**
     * 右侧文字点击事件
     */
    $_rightTextClick() {
      this.$emit("rightText");
    }
  }

}
